.maincontainer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  align-self: stretch;
  border: 3px solid #918ca7;
  gap: 0.5rem;
  padding: 0.5rem;
  margin: 1rem;
  background-color: #171721;
  height: 32rem;
}

.columnBlockLeft{
  display: flex;
  flex-flow: column;
  min-width: 20%;
  max-width: 20%;
  align-self: stretch;
  justify-content: center;
  align-items: center;
  border: 3px solid #918ca7;
}

.columnBlockCenter{
  display: flex;
  flex-flow: column;
  min-width: 25%;
  max-width: 25%;
  align-self: stretch;
  justify-content: center;
  align-items: center;
  border: 3px solid #918ca7;
}

.columnBlockRight{
  display: flex;
  flex-flow: column;
  min-width: 50%;
  max-width: 50%;
  align-self: stretch;
  justify-content: center;
  align-items: center;
  border: 3px solid #918ca7;
}

.lines{
  display: flex;
  flex-wrap: wrap;
  align-self: stretch;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 0 0.3rem;
  margin: 0 0.3rem;
}

.linesText{
  display: flex;
  align-self: stretch;
  align-items: center;
  gap: 1rem;
  padding: 0 0.3rem;
  margin: 0 0.3rem;
}

.linesTextCH {
  display: flex;
  justify-content: space-between;
  align-self: stretch;
  align-items: center;
  gap: 1rem;
  padding: 0.2rem 0.3rem;
  margin: 0 0.2rem;
  border: 1px solid #918ca7;
}

.columns{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
  align-self: stretch;
  gap: 0.3rem;
  margin: 0.3rem 0;
  padding: 0.3rem 0;
}

.columnsText{
  display: flex;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  flex-flow: column;
  align-self: stretch;
  margin: 0.2rem 0.2rem;
  padding: 0.2rem 0.2rem;
  border: 1px solid #918ca7;
}

.stat{
  cursor: pointer;
}

.mainStat{
  cursor: pointer;
  color: gold;
}

.gold{
  color: gold;
}

.lumber {
  color: greenyellow;
}

.inactive {
  color: gray;
}

.red {
  color: lightcoral;
}

.blue {
  color: lightblue;
}

.smallImg{
  width: 48px;
}

.textCenter{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 2.8rem;
}